import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { getComplitedInfoCompany, GetComplitedInfoCompanyRequestType } from "../../store/features/complitedThunks";
import { RootState, useAppDispatch } from "../../store/store";
import Preloader from "../common/preloader/preloader";
import cardStyles from "./card.module.css";
import ChangeZone from "./changeZone";
import DeleteCompany from "./DeleteCompany";
import QA from "./QA";
import SupplierExistedRisk from "./supplierExistedRisk";
import SupplierHead from "./supplierHead";

const SupplierCard = () => {
    const { supplierId } = useParams();
    const dispatch = useAppDispatch();
    const {t} = useTranslation();

    useEffect(() => {
        const dataToSend: GetComplitedInfoCompanyRequestType = {
            questionaryId: supplierId || "",
            t: t
        }
        dispatch(getComplitedInfoCompany(dataToSend));
    }, [])

    const isLoading: boolean = useSelector((state:RootState) => state.complited.isLoading);
    const onRequestDataError: string = useSelector((state:RootState) => state.complited.errors.onRequestDataError);

    return <div className={cardStyles.cardWrapper}>

        supplier CARD #{supplierId}
        {
            isLoading
                ? <Preloader />
                : onRequestDataError.length > 0
                    ?   <div className={cardStyles.onRequestDataError}>
                            {onRequestDataError}
                        </div>
                    :   <>
                            <SupplierHead />
                            {/* { checkedDate.length > 0 && <SupplierExistedRisk/> } */} <SupplierExistedRisk/>
                            <QA />
                            <ChangeZone />
                            <DeleteCompany />
                        </>
        }

    </div>
}

export default SupplierCard;
